.bar {
    display: flex;
    width: fit-content;

    margin: 1rem auto;
    padding: 0;
    gap: 2rem;
    justify-content: space-around;

    list-style: none;
}

.bar.horizontal {
    flex-direction: row;
}

.bar.vertical {
    flex-direction: column;
}

.bar-item {
    margin: auto;
}

.bar-item * {
    margin: auto;
}